@import './mixins';
@import './theme';

@property --angle {
  syntax: '<angle>';
  initial-value: 0deg;
  inherits: false;
}

#root .psm-swap-container {
  .psm-swap {
    background: $color-farming-card-bg;
    padding: 30px 40px;
    margin-bottom: 15px;
    text-align: center;
    overflow-wrap: break-word;
    margin: 0px;
    position: relative;
    z-index: 0;
    color: $color-text-default;
    border-radius: 10px;
    @include lightThemeOnly {
      box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
    }

    .psm-swap-box {
      display: flex;
      flex-direction: column;
      position: relative;

      .swap-arrow {
        margin: 24px 0;
        height: 46px;
        width: 46px;
        background-size: cover;
        background-repeat: no-repeat;
        cursor: pointer;
        background-image: url(../images/psm/icon_swap_to.svg);
        @include lightThemeOnly {
          background-image: url(../images/psm/icon_swap_to_light.svg);
          &:hover {
            background-image: url(../images/homeTokens/icon_swap_active_light.svg);
          }
        }
        &:hover {
          background-image: url(../images/homeTokens/icon_swap_active.svg);
        }
      }

      .swap-input-box {
        .swap-input-title {
          justify-content: space-between;
          align-items: center;
          display: flex;
          flex-direction: row;

          .swap-input-title-text {
            font-weight: 700;
            font-size: 28px;
            line-height: 22px;
            color: #ffffff;
          }

          .title-info {
            display: flex;
            flex: 1;
            width: 0;
            flex-wrap: wrap;
            justify-content: flex-end;
            .input-icon-tip {
              margin-right: 6px;
            }

            .swap-balance-text {
              font-size: 14px;
              font-weight: 500;
              color: $color-text-default;
              opacity: 0.6;
            }

            .swap-balance-value {
              max-width: 100%;
              font-size: 14px;
              font-weight: 600;
              color: $color-text-default;
              margin-left: 6px;
              white-space: normal;
              text-align: right;
              // display: inline-flex;
              // align-items: center;

              span {
                margin-right: 5px;
              }

              img {
                margin-left: 5px;
              }
            }
          }

          .ant-select {
            .ant-select-selection-item,
            .ant-select-item-option {
              img {
                width: 20px;
                height: 20px;
                margin-right: 8px;
                // border: 1px solid #ddd;
                // background: #fff;
              }
            }
          }
        }

        .swap-input {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 15px;
          background: $color-input-bg;
          border-radius: 10px;

          .ant-input {
            height: 64px;
            white-space: nowrap;
            // overflow: hidden;
            // text-overflow: ellipsis;
            background: transparent;
            border: none;
            padding: 14px 16px 16px 14px;
            font-weight: 700;
            font-size: 30px;
            line-height: 36px;
            color: $color-text-default;
            &::placeholder {
              font-size: 30px;
              // transform: translateY(-7px);
              // -ms-transform: translateY(-7px);
              // -webkit-transform: translateY(-7px);
              color: $color-text-default-3;
            }
          }

          .swap-input-btn-max {
            margin-right: 10px;
            padding: 4px 14px;
            background: $color-max-btn-bg;
            border-radius: 15px;
            border: 1px solid $color-max-button-border;
            font-weight: 600;
            font-size: 12px;
            color: $color-max-button-text;
            margin-left: 10px;
            white-space: nowrap;
            cursor: pointer;
          }
        }

        .ant-select {
          border: 0;
        }
        .ant-select-selector {
          background: transparent;
          border-color: transparent !important;
          .ant-select-selection-item {
            color: $color-text-default;
            font-weight: 600;
          }
        }
      }

      .swap-error-tip {
        margin-top: 13px;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        min-height: 20px;
        color: #e06b63;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        &.vhide {
          visibility: hidden;
        }
      }

      .ant-select-dropdown .ant-select-item {
        min-width: 122px !important;
      }
    }

    .swap-fee {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-top: 5px;
      font-size: 18px;
      min-height: 22px;
      img {
        margin-left: 5px;
        cursor: pointer;
      }
    }

    .submit-btn {
      background: $color-button-primary;
      box-shadow: 2px 2px 8px 0px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      font-style: normal;
      display: flex;
      align-items: center;
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      width: 100%;
      height: 70px;
      justify-content: center;
      margin-top: 40px;
      cursor: pointer;
      @include lightThemeOnly {
        color: #fff;
        &.disabled {
          color: rgba(255, 255, 255, 0.4);
        }
      }

      &.disabled {
        color: rgba(255, 255, 255, 0.4);
        cursor: not-allowed;
      }
    }
  }
}

.psm-detail-modal.ant-modal {
  width: 900px !important;
  .ant-modal-content {
    // background: linear-gradient(180deg, #222229 0%, #18181F 100%);
    background: #222229;
    border-radius: 20px;
    color: #fff;
    .ant-modal-body {
      padding: 40px !important;
      min-height: 420px;
      .psm-amount-modal-content {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        .top {
          padding: 60px 0 36px 50px;
          text-align: left;
          width: 100%;
          background: url(../images/psm/bg-j.jpg) no-repeat;
          background-size: auto 100%;
          .tip {
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
            color: rgba(255, 255, 255, 0.6);
            display: inline-flex;
            align-items: center;
            img {
              margin-left: 5px;
            }
          }
          .balance {
            font-weight: 700;
            font-size: 44px;
            line-height: 53px;
            padding-left: 50px;
            margin-top: 13px;
            background: url(../images/psm/psm-usdd.svg) left no-repeat;
          }
        }
        .table {
          display: flex;
          align-items: center;
          flex-direction: column;
          background: #191a1a;
          border-radius: 16px;
          width: 100%;
          // padding: 0 30px;
          .tr {
            display: inline-flex;
            align-items: center;
            padding: 30px 60px;
            justify-content: space-between;
            width: 100%;
            border-bottom: 1px dashed rgba(255, 255, 255, 0.2);
            border-radius: 16px 16px 0 0;

            &.th {
              height: 40px;
              line-height: 40px;
              padding: 9px 60px;
              border-bottom: none;

              font-weight: 500;
              font-size: 16px;
              line-height: 22px;
              color: rgba(255, 255, 255, 0.7);
              background: rgba(0, 0, 0, 0.2);

              .td:nth-child(2) {
                transform: translateX(23px);
                -ms-transform: translateX(23px);
                -webkit-transform: translateX(23px);
              }
            }

            &:last-child {
              border-bottom: none;
            }

            .td {
              text-align: right;
              width: calc(33% - 33px);
              font-weight: 600;
              font-size: 14px;
              line-height: 19px;
              img {
                margin-top: -3px;
                margin-left: 7px;
              }

              &:first-child {
                width: 71px;
                text-align: left;
              }

              &.link {
                color: #746bb7;
                cursor: pointer;
                background: url(../images/psm/regular-link.svg) 44px 2px no-repeat;
                padding-right: 16px;
                a {
                  color: #746bb7;
                }

                &:hover {
                  background: url(../images/psm/hover-link.svg) 44px 2px no-repeat;
                  a {
                    color: #998fdf;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .ant-modal-close {
    .ant-modal-close-x {
      color: #fff;
    }
    // position: absolute;
    // top: 0;
    // right: 0;
    // z-index: 10;
    // padding: 0;
    // color: rgba(0, 0, 0, 0.45);
    // font-weight: 700;
    // line-height: 1;
    // text-decoration: none;
    // background: transparent;
    // border: 0;
    // outline: 0;
    // cursor: pointer;
    // transition: color 0.3s;
    // min-width: unset;
  }
}
.psm-contracts-modal.ant-modal {
  .content {
    max-height: 420px;
    overflow: auto;
    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
      background: transparent;
    }

    &::-webkit-scrollbar-thumb {
      background: transparent;
      border-radius: 10px;
    }

    &:hover::-webkit-scrollbar-thumb {
      background: #dedede;
    }

    &:hover::-webkit-scrollbar-track {
      background: transparent;
    }
    .item {
      text-align: left;
      .item-title {
        font-size: 16px;
        font-weight: 600;
        color: $color-text-default;
        line-height: 22px;
      }
      .ul {
        .li {
          background: $color-info-bg;
          border-radius: 5px;
          border: 1px solid rgba($color-text-default, 0.1);
          margin-top: 10px;
          padding: 12px;
          position: relative;
          @include lightThemeOnly {
            border: 1px solid #e5e5e5;
          }
          > div {
            display: flex;
            justify-content: space-between;
            align-items: center;
            img {
              cursor: pointer;
            }
          }
          .name {
            font-size: 16px;
            font-weight: 600;
            color: $color-text-default;
            line-height: 22px;
            > span:nth-child(2) {
              white-space: nowrap;
            }
          }
          .address {
            font-size: 14px;
            font-weight: 600;
            color: $color-button-link-text;
            line-height: 20px;
            word-break: break-all;
          }
          .aaa {
            top: 0;
            right: 0px;
          }
        }
      }
    }
    .tip {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
      line-height: 18px;
      text-align: left;
      margin: 20px 0;
    }
  }
}

.page-psm {
  margin: 0 auto;
  max-width: 1200px;
  @include common-content-width;
  .page-psm-title {
    padding-top: 56px;
  }

  .page-psm-main {
    display: flex;
  }

  .page-psm-main-l {
    position: relative;
    margin-right: 30px;
    flex: 1;
  }

  .page-psm-main-r {
    position: relative;
    width: 460px;
    .psm-desc-card {
      padding: 20px;
      margin-bottom: 20px;
      border-radius: 10px;
      background: $color-modal-bg url(../images/psm/bg_intro1.png) no-repeat;
      background-position: top right;
      background-size: auto 100%;
      &:nth-child(2) {
        background-image: url(../images/psm/bg_intro2.png);
      }
      &:nth-child(3) {
        background-image: url(../images/psm/bg_intro3.png);
      }
    }
    .psm-desc-card-title {
      font-size: 20px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $color-text-default;
      line-height: 28px;
    }
    .psm-desc-card-desc {
      margin-top: 16px;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
      line-height: 20px;
      @include lightThemeOnly {
        opacity: 0.9;
      }
    }

    .psm-des-jumper {
      display: flex;
      .psm-des-item {
        margin-right: 30px;
        cursor: pointer;
        &:last-child {
          margin-right: 0;
        }
      }
      .psm-des-btn {
        display: flex;
        align-items: center;
        span {
          margin-right: 10px;
          font-size: 14px;
          font-weight: 600;
          color: $color-text-default;
          font-family: PingFangSC-Semibold, PingFang SC;
          line-height: 20px;
        }
        img {
          display: block;
          width: 28px;
          height: 28px;
        }
      }
    }
  }

  .convert-usdd {
    display: flex;
    align-items: center;
    color: $color-text-default;
    position: absolute;
    top: -44px;
    right: 0;
    cursor: pointer;
    @include mobile-only {
      position: inherit;
      top: 0;
      margin-bottom: 16px;
    }
    > span {
      font-size: 14px;
      line-height: 20px;
      margin-right: 10px;
    }
    > img {
      width: 24px;
      height: 24px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .page-psm {
    padding: 0 20px;
    max-width: 100%;
  }
}

@media screen and (max-width: 1079px) {
  #root {
    .psm-swap-container {
      .psm-swap {
        padding: 20px 15px;
        .psm-swap-box {
          .swap-input-box {
            .swap-input-title {
              .title-info {
                .swap-balance-value {
                  font-size: 12px;
                  line-height: 17px;
                }
              }
            }

            .swap-input {
              margin-top: 10px;
              .ant-input {
                padding: 10px;
                height: 44px;
                font-size: 20px;
                line-height: 24px;
                &::placeholder {
                  font-size: 20px;
                }
              }
            }
          }

          .swap-arrow {
            margin: 16px 0;
            width: 30px;
            height: 30px;
            &:hover {
              background-image: url(../images/psm/icon_swap_to.svg);
            }
          }
        }
        .submit-btn {
          margin-top: 20px;
          font-size: 16px;
          line-height: 22px;
          height: 40px;
        }
      }
    }
  }
  .page-psm {
    padding: 0 15px;
    .page-psm-title {
      padding-top: 10px;
    }
    .page-psm-main {
      display: block;
    }
    .page-psm-main-l {
      margin-right: 0;
    }
    .page-psm-main-r {
      margin-top: 30px;
      width: 100%;
      .psm-desc-card {
        margin-bottom: 14px;
        padding: 12px 10px;
        .psm-desc-card-title {
          font-size: 16px;
          line-height: 22px;
        }
        .psm-desc-card-desc {
          margin-top: 10px;
          font-size: 14px;
          line-height: 20px;
        }
      }
      .psm-des-jumper {
        flex-wrap: wrap;
        margin-top: 20px;
        .psm-des-item {
          margin-bottom: 20px;
        }
      }
    }
  }
}
